/* eslint-disable no-param-reassign */
export const getWinLine = (steps, currentStep) => {
  // Case 1: check i
  const xSteps = steps
    .filter((step) => step.j === currentStep.j && step.value === currentStep.value)
    .sort((a, b) => a.i - b.i);
  if (xSteps.length >= 5) {
    let minI = xSteps.findIndex((step) => step.i === currentStep.i);
    while (xSteps[minI - 1] && xSteps[minI].i - xSteps[minI - 1].i === 1) minI -= 1;
    let maxI = xSteps.findIndex((step) => step.i === currentStep.i);
    maxI += 1;
    while (xSteps[maxI] && xSteps[maxI].i - xSteps[maxI - 1].i === 1) maxI += 1;
    if (maxI - minI >= 5) return xSteps.slice(minI, maxI);
  }

  // Case 2: check j
  const ySteps = steps
    .filter((step) => step.i === currentStep.i && step.value === currentStep.value)
    .sort((a, b) => a.j - b.j);
  if (ySteps.length >= 5) {
    let minJ = ySteps.findIndex((step) => step.j === currentStep.j);
    while (ySteps[minJ - 1] && ySteps[minJ].j - ySteps[minJ - 1].j === 1) minJ -= 1;
    let maxJ = ySteps.findIndex((step) => step.j === currentStep.j);
    maxJ += 1;
    while (ySteps[maxJ] && ySteps[maxJ].j - ySteps[maxJ - 1].j === 1) maxJ += 1;
    if (maxJ - minJ >= 5) return ySteps.slice(minJ, maxJ);
  }

  // Case 3: check i - j
  const xySteps = steps
    .filter(
      (step) =>
        step.i - step.j === currentStep.i - currentStep.j && step.value === currentStep.value,
    )
    .sort((a, b) => a.i - b.i);
  if (xySteps.length >= 5) {
    let minI = xySteps.findIndex((step) => step.i === currentStep.i);
    while (xySteps[minI - 1] && xySteps[minI].i - xySteps[minI - 1].i === 1) minI -= 1;
    let maxI = xySteps.findIndex((step) => step.i === currentStep.i);
    maxI += 1;
    while (xySteps[maxI] && xySteps[maxI].i - xySteps[maxI - 1].i === 1) maxI += 1;
    if (maxI - minI >= 5) return xySteps.slice(minI, maxI);
  }

  // Case 4: check j
  const yxSteps = steps
    .filter(
      (step) =>
        step.i + step.j === currentStep.i + currentStep.j && step.value === currentStep.value,
    )
    .sort((a, b) => a.i - b.i);
  if (yxSteps.length >= 5) {
    let minI = yxSteps.findIndex((step) => step.i === currentStep.i);
    while (yxSteps[minI - 1] && yxSteps[minI].i - yxSteps[minI - 1].i === 1) minI -= 1;
    let maxI = yxSteps.findIndex((step) => step.i === currentStep.i);
    maxI += 1;
    while (yxSteps[maxI] && yxSteps[maxI].i - yxSteps[maxI - 1].i === 1) maxI += 1;
    if (maxI - minI >= 5) return yxSteps.slice(minI, maxI);
  }

  return null;
};

export function minimax(game, depth, isMaximizingPlayer, alpha, beta) {
  const lastStep = game.histories[game.histories.length - 1];
  const winLine = getWinLine(game.histories, lastStep);
  if (winLine) {
    return {
      ...lastStep,
      value: isMaximizingPlayer ? 5 : -5,
    };
  }
  if (depth === 0) {
    return {
      ...lastStep,
      value: 0,
    };
  }

  const board = game.board.map((row) => [...row]);
  const nextSteps = [];
  game.histories.forEach((step) => {
    for (let i = step.i - 4; i < step.i + 5; i++) {
      for (let j = step.j - 4; j < step.j + 5; j++) {
        if (i >= 0 && j >= 0 && i < board.length && j < board[0].length && !board[i][j]) {
          board[i][j] = -lastStep.value;
          nextSteps.push({ i, j, value: -lastStep.value });
        }
      }
    }
  });

  if (isMaximizingPlayer) {
    let best = {
      i: -1,
      j: -1,
      value: -100,
    };
    for (let i = 0; i < nextSteps.length; i++) {
      const newBoard = game.board.map((row) => [...row]);
      newBoard[nextSteps[i].i][nextSteps[i].j] = nextSteps[i].value;
      const histories = [...game.histories];
      histories.push(nextSteps[i]);
      const step = minimax(
        {
          histories,
          board: newBoard,
        },
        depth - 1,
        false,
        alpha,
        beta,
      );

      best = best.i >= 0 && best.value > step.value ? best : step;
      alpha = Math.max(alpha, best.value);
      // Alpha Beta Pruning
      if (best.i >= 0 && beta <= alpha) break;
    }
    return best;
  }

  // beta
  let best = {
    i: -1,
    j: -1,
    value: +100,
  };
  for (let i = 0; i < nextSteps.length; i++) {
    const newBoard = game.board.map((row) => [...row]);
    newBoard[nextSteps[i].i][nextSteps[i].j] = nextSteps[i].value;
    const histories = [...game.histories];
    histories.push(nextSteps[i]);
    const step = minimax(
      {
        histories,
        board: newBoard,
      },
      depth - 1,
      true,
      alpha,
      beta,
    );
    best = best.i >= 0 && best.value < step.value ? best : step;
    beta = Math.min(beta, best.value);
    // Alpha Beta Pruning
    if (best.i >= 0 && beta <= alpha) break;
  }
  return best;
}
