/* eslint-disable react/no-array-index-key */
import React, { useRef, useState } from 'react';
import Grid from '@material-ui/core/Grid';
import useStyles from './style';
import { getWinLine, minimax } from './game';

export default function CaroGame() {
  const classes = useStyles();
  const game = useRef({
    isEnd: false,
    histories: [],
    turn: -1,
  });
  const display = {
    [-1]: 'O',
    0: ' ',
    1: 'X',
  };
  const [winLine, setWinLine] = useState([]);
  const [turn, setTurn] = useState(-1);
  const [board, setBoard] = useState(
    new Array(19).fill(0).map(() => new Array(15).fill(0)),
  );
  const setCel = (cel, i, j) => {
    if (game.current.isEnd) {
      return;
    }
    if (!cel) {
      const newBoard = [...board];
      newBoard[i][j] = turn;
      const step = { i, j, value: turn };
      game.current.histories.push(step);
      setTurn(-turn);
      const winLn = getWinLine(game.current.histories, step);
      if (winLn) {
        game.current.isEnd = !!winLn;
        setWinLine(winLn);
      }
      setBoard(newBoard);
    }
  };

  const resetGame = () => {
    game.current.histories.length = 0;
    game.current.isEnd = false;
    setBoard(new Array(19).fill(0).map(() => new Array(15).fill(0)));
  };
  const switchTurn = () => {
    if (game.current.histories.length === 0) setTurn(-turn);
  };

  const findSolution = () => {
    const step = minimax(
      {
        board: board.map((row) => [...row]),
        histories: [...game.current.histories],
      },
      6,
      true,
      -5,
      5,
    );
    console.log('Result', step);
  };

  return (
    <Grid container className={classes.root}>
      <Grid container spacing={1}>
        <Grid item xs={3} className={classes.controlSection}>
          <button
            type="button"
            className={classes.control}
            onClick={() => resetGame()}
          >
            New Game
          </button>
          <button
            type="button"
            className={classes.control}
            onClick={() => switchTurn()}
          >
            Switch Turn {display[turn]}
          </button>
          <button
            type="button"
            className={classes.control}
            onClick={() => findSolution()}
          >
            Next Step
          </button>
        </Grid>
        <Grid item xs={6} className={classes.gameBoard}>
          <table className={classes.board}>
            <tbody>
              <tr key={-1} className={classes.boardRow}>
                <td className={classes.headCell} key={0}>
                  {' '}
                </td>
                {board[0].map((_, j) => (
                  <td className={classes.headCell} key={j}>
                    {j}
                  </td>
                ))}
              </tr>
              {board.map((row, i) => (
                <tr key={i} className={classes.boardRow}>
                  <td>{i}</td>
                  {row.map((cel, j) => (
                    <td
                      key={j}
                      className={
                        game.current.isEnd &&
                        winLine.find((step) => step.i === i && step.j === j)
                          ? classes.boardCellHightLight
                          : classes.boardCell
                      }
                    >
                      <button
                        type="button"
                        onClick={() => setCel(cel, i, j)}
                        className={classes[`caroCell${cel + 1}`]}
                      >
                        {display[cel]}
                      </button>
                    </td>
                  ))}
                </tr>
              ))}
            </tbody>
          </table>
        </Grid>
      </Grid>
    </Grid>
  );
}
